.navbar {
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
    width: 100vw;
    height: 200px;
    margin-top: 0;
    padding: 0 20px;
}

.navbar_items {
    margin-left: 15px;
    margin-right: 50px;
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
    color: white;
    text-decoration: none;
    font-size: 30px;
}

.logo {
    width: 144px;
    height: 144px;
    margin-right: 50px;
}