* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    width: 100%;
    height: 100vh;
    background: url("../assets/bg.jpg") no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

.App {
    width: 100vw;
}

.post {
    width: 95vw;
    display: flex;
    padding: 15px;
    border: 2px solid white;
    border-radius: 12px;
    margin-top: 30px;
    margin-left: 40px;
    justify-content: space-between;
    align-items: center;
    font-family: 'Raleway', sans-serif;
    color: black;
    backdrop-filter: blur(10px);
}

.listStyle {
    margin-top: 30px;
    font-family: 'Raleway', sans-serif;
}

.not-found {
    text-align: center;
    font-family: 'Raleway', sans-serif;
}

.post-enter {
    transform: translateX(-1000px);
}

.post-enter-active {
    transform: translateX(0px);
    transition: all 500ms ease-in;
}

.post-exit {
    opacity: 1;
}

.post-exit-active {
    transform: translateX(-1000px);
    transition: all 500ms ease-in;
}

.page_wrapper {
    margin-top: 50px;
    margin-bottom: 50px;
    display: flex;
    justify-content: center;
}

.page {
    display: flex;
    margin-right: 15px;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border: 2px solid white;
    border-radius: 12px;
    background-color: rgba(0,0,0,0);
    font-family: 'Raleway', sans-serif;
    cursor: pointer;
    color: white;
}

.page_current {
    background-color: rgba(255,255,255,0.7);
    color: black;
}