.myModal {
    z-index: 1;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0,0,0,0.3);
    backdrop-filter: blur(10px);
}

.myModalContent {
    padding: 25px;
    background-color: rgba(0,0,0,0);
    border: 1px solid white;
    border-radius: 8px;
    min-width: 250px;
}

.myModal.active {
    display: flex;
    justify-content: center;
    align-items: center;
}