.myBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 40px;
    width: 150px;
    height: 50px;
    border: 2px solid white;
    border-radius: 8px;
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
    cursor: pointer;
    background-color: rgba(0,0,0,0);
    color: white;
    backdrop-filter: blur(10px);
}

.myBtn:hover {
    border: 2px solid lightgray;
}

.myBtn:disabled {
    border: 2px solid #c1c1e8;
    cursor: default;
}