.myInput {
    box-sizing: border-box;
    width: 500px;
    padding: 5px 15px;
    margin-top: 20px;
    margin-left: 40px;
    margin-bottom: 20px;
    border: 2px solid white;
    border-radius: 8px;
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
    background-color: rgba(0,0,0,0);
    backdrop-filter: blur(10px);
    color: white;
}

.myInput::placeholder {
    color: white;
}

.myInput:focus {
    border-color: black;
}